<template>
    <div class="black-bg" v-if="모달창열렸니==true">
    <div class="white-bg">
      <img :src="여행지[제목누른거].image" style="width:100%">
      <h4>{{여행지[제목누른거].title}}</h4>
      <p>{{여행지[제목누른거].content}}</p>
      <p>{{여행지[제목누른거].price.toLocaleString()}}원</p>
      <!-- @input @click이랑 같은 거 -->
      <!-- <input @input="month=$event.target.value"> -->
      <input v-model.number="priceNumer">
      <p>{{ priceNumer }}개를 선택함 :{{여행지[제목누른거].price * priceNumer}}원</p>
      <p>상세 페이지 내용</p>
      <!-- read only기능 -->
      <button @click="$emit('closeModal')">닫기</button>
    </div>

  </div>
</template>

<script>
// import { emit } from 'process';


export default{
    name:"ModalChang",

    data(){
      return{
        priceNumer:0
      }
    },
      watch:{
        priceNumer(a){ //프라이넘버데이터가 바뀔 때 마다
          //만약에 10개 이상 구매하면 11개 이상 예약 못합니다.라는 문구 출력
          if(a>11){
            alert("11개 이상은 선택할 수 없습니다.")
          }
          //문자방지코드 문자입력시 ===> 문자입력하지마세요
          //이후에 값을 1로 되돌림
          if(isNaN(a)==true){
            alert("문자입력하지마세요")
            this.priceNumer=1; //밖에 값을 가져올땐 항상 this를 써야함
            
          }
        } 
      },
    
    props:{
        
            여행지:Array,   //부모님이 가지고 있는 데이터의 원형 ====> 데이터이름:자료형
        제목누른거:Number,
        모달창열렸니:Boolean,
        
    }
    
    
}
    


</script>
<style>
/* 모달창 CSS */
body{margin: 0; padding: 0;}
div{box-sizing: border-box;}
.black-bg{width: 100%; height: 100%;
background: rgba(0,0,0,0.5);
position: fixed; padding: 20px;}
.white-bg{width: 100%; background: white;
border-radius: 8px;
padding: 20px;}</style>